:root {
  --primary-font:"Source Code Pro", monospace;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --primary-color: #A4FFAF;
  --light-black: #6a6a6a;
  --dim-black: #000000;
  --md-color: #F9CD69;
  --green-color: #38b000;
  --red-color: #d90429;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--primary-font);
  background-color: var(--dim-black);
  color: #fff;
  text-transform: capitalize;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.container {
  max-width: 450px;
  margin: 2.5rem auto;
  align-items: center;
  text-align: center;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
}
.container h4 {
  color: var(--light-black);
  margin-top: 6.25rem;
}
.container .generated_pass_input--ctn {
  position: relative;
  display: flex;
  width: 100%;
  justify-content: center;
}
.container .generated_pass_input--ctn input {
  width: 100%;
  cursor: none;
  pointer-events: none;
  background-color: rgb(24, 20, 20);
  color: white;
  padding: 17px;
  font-size: 1.4rem;
  border: none;
  outline: none;
  transition: all 400ms ease-in-out;
}
.container .generated_pass_input--ctn input:focus {
  border: none;
  outline: 1px solid var(--primary-color);
}
.container .generated_pass_input--ctn i {
  position: absolute;
  right: 14px;
  top: 14px;
  font-size: 1.6rem;
  cursor: pointer;
  color: var(--primary-color);
  transition: all 200ms ease-in-out;
}
.container .generated_pass_input--ctn i:hover {
  opacity: 0.7;
}
.container .generator_tools_ctn {
  background-color: rgb(24, 20, 20);
  margin-top: 1.25rem;
  padding-bottom: 30px;
}
.container .generator_tools_ctn--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.container .generator_tools_ctn--header_number {
  color: var(--primary-color);
  font-weight: 600;
  font-size: 1.45rem;
}
.container .generator_tools_ctn input[type=range] {
  position: relative;
  cursor: pointer;
  -webkit-appearance: none;
  margin: 10px auto;
  -moz-appearance: none;
  display: block;
  width: 90%;
  height: 8px;
  background-color: #d5d5d5;
  border-radius: 8px;
  outline: none;
  transition: all 700ms ease-in-out;
}
.container .generator_tools_ctn input[type=range]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 8px;
  transition: all 700ms ease-in-out;
}
.container .generator_tools_ctn input[type=range]::-moz-track {
  -moz-appearance: none;
  height: 8px;
  transition: all 700ms ease-in-out;
}
.container .generator_tools_ctn input[type=range]::-ms-track {
  appearance: none;
  height: 8px;
  transition: all 700ms ease-in-out;
}
.container .generator_tools_ctn input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 22px;
  width: 22px;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -6px;
  border: none;
  transition: all 200ms ease-in-out;
}
.container .generator_tools_ctn input[type=range]::-moz-range-thumb {
  -webkit-appearance: none;
  height: 22px;
  width: 22px;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -6px;
  border: none;
  transition: all 700ms ease-in-out;
}
.container .generator_tools_ctn input[type=range]::-ms-thumb {
  appearance: none;
  height: 22px;
  width: 22px;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -6px;
  border: none;
}
.container .generator_tools_ctn input[type=range]:active::-webkit-slider-thumb {
  background-color: #ffffff;
  border: 1px solid var(--primary-color);
}
.container .generator_tools_ctn--checkbox_ctn {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding: 0 20px;
  padding-top: 15px;
}
.container .generator_tools_ctn--checkbox_ctn .custom_checkbox_ctn {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.container .generator_tools_ctn--checkbox_ctn .custom_checkbox_ctn input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.container .generator_tools_ctn--checkbox_ctn .custom_checkbox_ctn .custom_checkbox {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: transparent;
  border: 1px solid #fff;
  transition: all 150ms ease-in;
}
.container .generator_tools_ctn--checkbox_ctn .custom_checkbox_ctn input:checked ~ .custom_checkbox {
  background-color: var(--primary-color);
}
.container .generator_tools_ctn--checkbox_ctn .custom_checkbox_ctn input:checked ~ .custom_checkbox::after {
  display: block;
}
.container .generator_tools_ctn--checkbox_ctn .custom_checkbox_ctn .custom_checkbox::after {
  content: "";
  position: absolute;
  display: none;
}
.container .generator_tools_ctn--checkbox_ctn .custom_checkbox_ctn .custom_checkbox::after {
  left: 5.5px;
  top: 2px;
  width: 6.5px;
  height: 12px;
  border: solid var(--black);
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.container .generator_tools_ctn--pass_strength_indi {
  padding: 0 20px;
  background-color: var(--black);
  display: flex;
  align-items: center;
  width: 90%;
  height: 50px;
  margin: 15px auto;
  justify-content: space-between;
  text-transform: uppercase;
}
.container .generator_tools_ctn--pass_strength_indi .strength {
  color: var(--light-black);
  font-weight: 700;
}
.container .generator_tools_ctn--pass_strength_indi .indicator {
  display: flex;
  align-items: center;
  position: relative;
}
.container .generator_tools_ctn--pass_strength_indi .indicator p {
  font-size: 1.2rem;
  margin-right: 3px;
}
.container .generator_tools_ctn--pass_strength_indi .indicator span {
  display: block;
  height: 30px;
  width: 12.5px;
  background-color: transparent;
  border: 1px solid #fff;
  margin-left: 7px;
}
.container .generator_tools_ctn button {
  border: none;
  cursor: pointer;
  outline: none;
  width: 90%;
  padding: 15px;
  margin-top: 5px;
  color: var(--black);
  font-weight: 800;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  transition: all 300ms ease-in-out;
  background-color: var(--primary-color);
}
.container .generator_tools_ctn button:hover {
  opacity: 0.7;
}
.container .generator_tools_ctn button i {
  font-size: 1.2rem;
  font-weight: 800;
}