html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
    list-style: none;
    // outline: 1px solid red; // (For css debugging :) )
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--primary-font);
    background-color: var(--dim-black);
    color: #fff;
    text-transform: capitalize;
}